@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Poltawski+Nowy&family=Roboto&display=swap');

/*font-family: 'Crimson Text', serif; --heading
font-family: 'Roboto', sans-serif; --general
font-family: 'Poltawski Nowy', serif; --brand name*/

*{
    margin:0px;
    padding: 0px;
    box-sizing:border-box;
    font-family: 'Roboto', sans-serif;
}
.container{
    ---bs-gutter-x:0;
}
/* Footer */
.link,.footer{
    color: #d3d3d3;
}
.link:hover{
    color: #d3d3d3;
    border-bottom:0.5px solid #d3d3d3;
}
/*header */
.brandLogo{
    width:3rem;
    margin-right: 10px;
    border-radius: 10px;
    mix-blend-mode:lighten;
}
.navbar-brand{
    font-family: 'Poltawski Nowy', serif;
    font-weight: 700;
    letter-spacing: 2px;
}
.navbar-toggler{
    padding:4px !important;
}
.navbar{
    box-shadow:0 8px 6px -6px gray;
    -webkit-box-shadow:0 8px 6px -6px gray;
    border-bottom:0.5px solid gray !important;
    background: #89CFF0 !important;
}
.nav-link{
    font-weight: 100;
    color:#5c5c5c;
    text-transform: uppercase;
}
.dropdown-item{
    text-transform: uppercase;
}
.close{
    font-size: 30px;
    display:none;
}
.hide{
    display:none;
}
.unhide{
    display:block;
}

/*page not found */
.pnf{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:80vh;
}
.pnf-title{
    font-size: 100px;
    color:	#292929;
    font-weight: 800;
}
.pnf-heading{
    color:	#363636;
    font-weight: 400;
}
.pnf-btn{
    color:	#363636;
    text-decoration: none;
    font-size: 20px;
    border: 0.5px solid #363636;
    border-radius:20px;
}
.pnf-btn:hover{
    color:#fff;
    background: #363636;
}

/*Contact page*/
.contact-heading{
    font-family: 'Crimson Text', serif;
}
.contactus{
    display:inline-flex;
    flex-direction: column;
    gap:20px;
    font-size: 35px;
    justify-content: center;
    align-items: center;
}
.location{
    width:300px;
    display: flex;
    flex-direction: row;
}
.contactus-heading,.contactus-paragh{
    font-size:1rem;
}
.contactus-heading{
    font-weight: 600;
}
.gps{
    border: 0.5px solid #363636;
    border-radius:50%;
    padding: 5px;
}
.gps:hover{
    transform: scale(1.2);
}
.card-form{
    border: 0.1px solid #363636;
    display: flex;
    border-radius: 10px;
    box-shadow: 0px 2px 5px 0px gray;
    flex-direction: column;
}
.card-form form{
    display:flex;
    flex-direction: column;
    gap:10px
}
.card-form form input{
    border-width: 0px !important;;
    border-bottom:0.5px solid #363636!important;
}
.send-btn{
    width:fit-content!important;
    border-radius: 10px;
    background: transparent;
    border: 0.5px solid #363636;
}
.send-btn:hover{
    background: #363636;
    color:#fff;
}
/* privacy policy */
.pp-heading,.about-heading,.register-title{
    font-size: 2rem;
    font-family: 'Crimson Text', serif;
}
.pp-paragh{
    font-size: 1rem;
}
/* about us */

.img img{
    width:30rem;
}

/* register page */
.register-image-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}
.register-image{
    float:right;
    width:18rem;
}
.register{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.register-form{
    width:70%;
}
.register-form-card{
    gap:10px;
    display: inline-flex;
    flex-direction: column;
}
.register-form-card input{
    border-width:0px !important;
    border-bottom: 0.5px solid #363636 !important;
}
.register-card-form{
    border: 0.2px solid #363636;
    border-radius: 10px;
    box-shadow: 0px 2px 5px 0px gray;
}
.register-card-title{
    font-size:1.5rem;
    font-family: 'Crimson Text', serif;
}
.register-text{
    text-align: center;
    font-size:0.8rem;
}

.Signup{
    width:fit-content;
    color:#363636;
    border: 0.2px solid #363636;
    border-radius: 10px;
    background: transparent;
}
.Signup:hover{
    background: #363636;
    color:#fff;
}
.forgot{
    font-size:12px;
}
/*admin dashboard*/
.border-start{
    border-color:#363636;
    border-width: 0.5px!important;
}
.admin-menu{
    font-size: 1.2rem;
}
.list-group-item{
    transition: all 0.3s;
}
.list-group-item:hover{
    color:#000;
    border:0.8px solid gray;
    box-shadow:0px 2px 5px 0px gray;
}
.admin-menu-heading,.admin-details-heading{
    font-size: 1.5rem;
    font-family: 'Crimson Text', serif;
}
.admin-details-heading{
    font-weight: 580;
}
.admin-details{
    font-weight: 200;
    text-decoration: underline;
    font-size: 1.4rem;
    font-family: 'Roboto', sans-serif; 
}
.list-group-item.active{
    background: #fff;
    color:#000;
    box-shadow:0px 2px 5px 0px gray;  
}
td,th{
    border-width: 0px !important;
}
tr{
    border-bottom: 0.2px solid gray;
}
.card-title , .card-text{
    color:#000;
    text-decoration: none!important;
}
.card-body{
    text-decoration: none;
}
a{
    text-decoration: none;
}

/*homePage*/
.home-start{
    background: #FAF9F6;
    height:15rem;
}
.home-start h1, .Profile-heading{
    font-family: 'Poltawski Nowy', serif !important;
    font-weight: 800;
    letter-spacing: 3px;
    color:#292929;
    margin:0px;
}

/*media query */
@media only screen and (max-width:500px) {
    .img img{
        width:20rem;
    }
    .list-group-item{
        text-align:center;
        padding:8px;
    }
    .register{
        align-items: center;
    }
    .register-image-form{
        align-items: center;
    }
    .register-image{
        text-align: center;
        width:12rem;
    }
    .admin-text{
        display: none;
    }
    .admin-details-heading{
        font-size: 1.3rem;
    }
    .admin-details{
        font-size:1.2rem
    }
    .admin-menu{
        font-size: 1.2rem;
        text-align: center;
    }
    .margin-small{
        margin-top: 10px;
        text-align: left!important;
    }
    .dynamic-flex{
        display:block!important;
    }
    .dynamic-flex input{
        margin-bottom: 0.5rem;
    }
    .small-set-width{
        width:100% !important;
    }
}